@import url(https://fonts.googleapis.com/css?family=Work+Sans&display=swap);
.todoItem {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: auto;
  font-size: 14px;
  padding: 10px 1rem;
  background: #fff; }
  .todoItem:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; }
  .todoItem:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px; }
  .todoItem:nth-child(even) {
    background: #f4f4f4; }

.deleteBtn {
  background: #ff0000;
  color: #fff;
  border: none;
  padding: 5px;
  border-radius: 50%;
  cursor: pointer; }

form {
  display: flex;
  margin: 1rem 0; }
  form input {
    flex: 10 1;
    padding: 10px 0; }
    form input::-webkit-input-placeholder {
      font-size: 12px; }
    form input::-moz-placeholder {
      font-size: 12px; }
    form input:-ms-input-placeholder {
      font-size: 12px; }
    form input::-ms-input-placeholder {
      font-size: 12px; }
    form input::placeholder {
      font-size: 12px; }
  form .btn {
    flex: 1 1; }

header {
  background: #333;
  color: #fff;
  text-align: center;
  padding: 10px; }
  header a {
    color: #fff;
    text-decoration: none; }
    header a:hover {
      color: #999; }
  header h1 {
    margin: 1rem 0; }
  header .navlink {
    padding: 1rem; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Work Sans', Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  margin: auto;
  max-width: 920px;
  padding: 0 4rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
}

.btn:hover {
  background: #444;
}

